.notfound {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.qcq {
    display: flex;
    justify-content: center;
    margin: 150px;
    height: 250px; 
}

.oups {
    color: #FF6060;
font-family: Montserrat;
font-size: 36px;
font-style: normal;
font-weight: 500;
line-height: 142.6%;
display: flex;
justify-content: center;
margin: 0;
}

.backHome {
    display: flex;
    justify-content: center;
    margin: 150px;    
    color: #000;
font-family: Montserrat;
font-size: 18px;
font-style: normal;
font-weight: 500;
line-height: 142.6%;
text-decoration-line: underline;
}

@media screen and (max-width: 768px) {
    .qcq {
        margin: 10%;
    }

    .oups {
        margin: 10%;
        font-size: 99%;
        text-align: center;
    }

    .backHome {
        margin: 10%;
        font-size: 99%;
        text-align: center;
    }
}